@import "../global/index";

.usda-share-icon {
  position: relative;
  margin: 0 0 0 rem(40);

  &.margin-right {
    margin-right: rem(32);
  }

  &.no-margin-left {
    margin-left: 0;
  }

  &.disabled {
    .usa-dt-picker__dropdown-container {
      button {
        cursor: default;
      }
    }
    .usa-dt-picker__dropdown-container,
    span {
      opacity: 0.5;
    }
  }

  @media(max-width: $medium-screen) {
    margin-right: rem(10);
    .usda-share-icon__share-text {
      &.keep-text {
          display: flex;
      }
      display: none;
    }
  }

  .usa-dt-picker {
    margin: 0;
    z-index: 30;

    .usa-dt-picker__dropdown-container {
      .usa-dt-picker__button {
        color: $gray-cool-10;

        svg {
          width: rem(24);
          height: rem(24);

          &:hover {
            color: $gray-cool-5;
          }

          &:active {
            color: $gray-cool-1;
          }
        }
      }

      .usa-dt-picker__list {
        width: rem(148);
        overflow: hidden;

        .usa-dt-picker__list-item {
          border-top: none;
          margin-bottom: rem(2);
        }

        .usa-dt-picker__list-item:focus {
          border-top: unset;
        }

        svg {
          color: $gray-cool-60;
          width: rem(16);
          height: rem(16);
          margin-top: rem(4);
        }

        span {
          color: $gray-cool-60;
          font-weight: $font-semibold;
          font-size: rem(16);
          line-height: rem(24);
          margin-left: rem(16);
        }

        .usa-dt-picker__item {
          padding-left: rem(16);
          padding-right: unset;
          padding-top: rem(8);
          padding-bottom: rem(8);
          width: 100%;
          display: flex;

          &:hover {
            background-color: $gray-cool-3;
          }
        }
      }
    }
  }

  .copy-confirmation {
    &.keep-text{
      @media(max-width:$medium-screen) {
        transform: translateX(120px);
      }
    }
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: rem(40);
    width: rem(140);
    background: $color-gray-lightest;
    border-radius: rem(3);
    color: $color-gray;
    font-size: rem(16);
    margin-right: 0;
    z-index: 31;

    // top and right are to position the msg over the copy item in the dropdown;
    // these are set for the instances in the sticky bar;
    // other instances not in stickyBar may need to override in their usas css
    top: rem(26);
    right: rem(8);

    svg {
      margin: 0 rem(8) 0 0;
      color: $green-cool-50;
    }
  }
}


